import VMath from './utils/vmath'
import initPlayer from './utils/audioplayer'

/*
mousecursor follows the mouse with a slower velocity and a bit of acceleration
import getMousePos from './utils/getMousePos'
 click cursor to play sound, now the cursor changes into pause
*/

export default class Cursor {
  constructor(w, h) {
    this.opacity = 0
    this.speed = 2.5
    this.pos = {
      x: w / 2,
      y: h / 2,
    }
    this.vel = {
      x: 0,
      y: 0,
    }
    this.acc = 2
    this.delta = 0.01
    // this.rotation = 0
    this.radius = 50
    this.force = 2
    this.audio = ''
  }

  // set _rotation(angle) {
  //   this.rotation = angle
  // }

  get _getPos() {
    return this.pos
  }

  get _getOpacity() {
    return this.opacity
  }

  fadeIn() {
    this.opacity < 1 ? (this.opacity += this.delta) : (this.opacity = 1)
  }

  fadeOut() {
    this.opacity > 0 ? (this.opacity -= this.delta * 2) : (this.opacity = 0)
  }

  follow(mouseCoords) {
    // get the distance (hypotenuse) and calculate the normal
    // add this somehow to accelerate (how?) with math.abs!
    const distance = VMath.distance(mouseCoords, this.pos)
    const normal = VMath.normalize(distance)

    const angle = VMath.angle(mouseCoords, this.pos)

    if (distance.x > 2 || distance.x < -2) {
      this.vel.x = Math.cos(angle) * this.speed
      this.pos.x += this.vel.x
    }

    if (distance.y > 2 || distance.y < -2) {
      this.vel.y = Math.sin(angle) * this.speed
      this.pos.y += this.vel.y
    }

    // this.rotation = angle
  }

  loadPlayer() {
    initPlayer()
  }

  draw(ctx, mouseOver) {
    !mouseOver ? this.fadeOut() : this.fadeIn()

    ctx.save()
    ctx.translate(this.pos.x, this.pos.y)
    // ctx.rotate(this.rotation)
    ctx.beginPath()
    ctx.fillStyle = `rgba(114, 127, 187, ${this.opacity})`
    ctx.arc(0, 0, this.radius, 0, Math.PI * 2)
    ctx.fill()
    ctx.restore()
  }
}
