import audioPlayer from 'web-audio-player'
import createAudioContext from 'ios-safe-audio-context'
import detectMediaSource from 'detect-media-element-source'
// create a loading event that triggers animation in cursor.js

function initPlayer() {
  // Create an iOS-safe AudioContext which fixes
  // potential sampleRate bugs with playback
  // (The hack needs to be called on touchend for iOS!)
  const audioContext = createAudioContext()

  // Detect whether createMediaElementSource() works
  // as expected. You can also use userAgent sniffing here.
  detectMediaSource(function(supportsMediaElement) {
    // No media element support -> we should buffer
    const shouldBuffer = !supportsMediaElement
    console.log(shouldBuffer)
    startPlayer(audioContext, shouldBuffer)
  }, audioContext)
}

function startPlayer(audioContext, shouldBuffer) {
  // add backup .ogg ?
  const sources = ['audio/voice.mp3']
  const modal = document.querySelector('.modal')

  const player = audioPlayer(sources, {
    context: audioContext,
    buffer: true,
    loop: false,
  })

  // This is triggered on mobile, when decodeAudioData begins.
  player.once('decoding', (amount) => {
    console.log(`amount: ${amount}`)
    modal.innerText = `...decoding: ${amount}`
  })

  // Only gets called when loop: false (in the render loop?)
  player.on('end', () => console.log('Audio ended'))

  player.on('error', (err) => console.log(`Error:${err.message}`))

  player.on('progress', (percentage, totalBytes) => {
    //probably here
    console.log(percentage, totalBytes)
  })

  // This is called with 'canplay' on desktop, and after decodeAudioData on mobile.
  player.on('load', () => {
    modal.style.display = 'none'

    console.log('Source:', player.element ? 'MediaElement' : 'Buffer')
    console.log('Playing', Math.round(player.duration) + 's of audio...')

    player.play()
  })
}

export default initPlayer
