import './index.scss'
// import VMath from './utils/vmath'
import Cursor from './cursor.js'
import { getMousePos } from './utils/getMousePos'
import VMath from './utils/vmath'

// todo - acceleration
// click:
// init webaudio on load
// load audio on click
// show loader (border around circle)

const c = document.querySelector('#canvas')
const ctx = c.getContext('2d')
const windowWidth = window.innerWidth
// const windowHeight = window.innerHeight

const aspectRatio = 1
const w = windowWidth * 0.66
const h = w * aspectRatio

const setCanvasDimensions = () => {
  c.width = w
  c.height = h
}

// global variables
let mouseOver = false
let t = 0
let tDelta = 0.1

// get coordinates
let mouseCoords = { x: 0, y: 0 }

const cursor = new Cursor(w, h)

const moveCanvas = (e) => {
  mouseCoords = getMousePos(e, c)
}

const enterCanvas = (e) => {
  mouseOver = true
}
const leaveCanvas = (e) => {
  mouseOver = false
}

const clearCanvas = () => {
  // Store the current transformation matrix
  ctx.save()

  // Use the identity matrix while clearing the canvas
  ctx.setTransform(1, 0, 0, 1, 0, 0)
  ctx.clearRect(0, 0, c.width, c.height)

  // Restore the transform
  ctx.restore()
}

const playVoice = (e) => {
  const dist = VMath.distance(mouseCoords, cursor.pos)
  // console.log(`x: ${Math.abs(parseInt(dist.x))}, y: ${Math.abs(parseInt(dist.y))}`)

  if (Math.abs(parseInt(dist.x)) < cursor.radius && Math.abs(parseInt(dist.y)) < cursor.radius) {
    cursor.loadPlayer()
  }
  // check mousecoords and coords of cursor + circumference
  // if inside circumference,
  // mouseCoords
}

const init = () => {
  setCanvasDimensions()
  window.addEventListener('resize', setCanvasDimensions)
  c.addEventListener('mouseenter', enterCanvas)
  c.addEventListener('mousemove', moveCanvas)
  c.addEventListener('mouseleave', leaveCanvas)
  c.addEventListener('click', playVoice)

  // smoothhack
  ctx.translate(0.5, 0.5)

  window.requestAnimationFrame(draw)
}

const draw = () => {
  t += tDelta
  clearCanvas()

  if (mouseOver) {
    cursor.fadeIn()
  } else if (cursor._getOpacity > 0) {
    cursor.fadeOut()
  }

  cursor.follow(mouseCoords)
  cursor.draw(ctx, mouseOver)

  window.requestAnimationFrame(draw)
}

init()
